import React from 'react'
import {
	createBrowserRouter,
	createRoutesFromElements,
	Route,
} from 'react-router-dom'

import MasterLayout from 'layouts/master/master_layout'
import LoginRoute from 'routes/login/login_route'
import SignUpRoute from 'routes/sign-up/sign_up_route'
import InterviewsRoute from 'routes/interviews/interviews_route'
import InterviewsCreateRoute from 'routes/interviews/interview_creation_route'
import InterviewTakenRoute from 'routes/interviews/interviews_taken_route'
import InterviewEditionRoute from 'routes/interviews/interview_edition_route'
import InterviewTakeRoute from 'routes/interviews/interview_take_route'
import PricingRoute from 'routes/pricing/pricing_route'

const router = createBrowserRouter(
	createRoutesFromElements(
		<>
			<Route path="/SignUp" element={<SignUpRoute />} />
			<Route path="/Login" element={<LoginRoute />} />
			<Route path="/" element={<MasterLayout />}>
				<Route index element={<InterviewsRoute />} />
				<Route path="/Pricing" element={<PricingRoute />} />
				<Route path="/Interviews" element={<InterviewsRoute />} />
				<Route path="/Interviews/:id" element={<InterviewTakenRoute />} />
				<Route path="/Interviews/create" element={<InterviewsCreateRoute />} />
				<Route path="/Interviews/edit/:id" element={<InterviewEditionRoute />} />
				<Route path="/Interviews/take/:id" element={<InterviewTakeRoute />} />
			</Route>
		</>
	)
)

export default router
