import React, { useEffect } from 'react'
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography'
import { Autocomplete, Chip } from "@mui/material"

import QuestionPanel from 'components/questions/questionsPanel';
import { useCreateInterview } from 'stores/create_interview_store'

const InterviewConfirmation = props => {
	const interview = useCreateInterview(state => state)

	const interviewType = interview.interviewsTypes.find(type => type.id === interview.initialSettings.interviewTypeId)
	const seniority = interview.seniorities.find(seniority => seniority.id === interview.initialSettings.seniorityId)

	// force scroll to top of screen
	useEffect(() => {
		window.scrollTo(0, 0)
	}, [])

	return (
		<>
			<Typography variant="h5" sx={{ mb: 4}} gutterBottom>
				Vista previa de su entrevista
			</Typography>
			<Grid container sx={{ mb: 4 }}>
					<Grid item xs={8} > 
						<TextField 
							sx={{ pr: 6 }}
							fullWidth 
							id="title" 
							disabled
							label="Título de la entrevista" 
							value={interview.initialSettings.title}
						/>
					</Grid>
					<Grid item xs={4}> 
						<TextField 
							fullWidth 
							id="date" 
							disabled
							label="Fecha"
							value={interview.initialSettings.date}
						/>
					</Grid>
			</Grid>	
			<Grid container sx={{ mb: 4 }}>
				<Grid item xs={4}> 
						<TextField 
							sx={{ pr: 6 }}
							fullWidth 
							id="type" 
							disabled
							label="Tipo de entrevista"
							value={interviewType.description}
						/>
					</Grid>
					<Grid item xs={4}> 
						<TextField 
							sx={{ pr: 6 }}
							fullWidth 
							id="type" 
							disabled
							label="Nivel de seniority"
							value={seniority.description}
						/>
					</Grid>
					<Grid item xs={4}> 
						<TextField 
							fullWidth 
							id="type" 
							disabled
							label="Area de trabajo"
							value={interview.initialSettings.area}
						/>
					</Grid>
			</Grid>	
			<Grid container>
					<Grid item xs={8} sx={{ pr: 6 }}> 
						<Autocomplete
							clearIcon={false}
							options={interview.categorySelection.selectedCategories}
							freeSolo
							multiple
							readOnly
							value={interview.categorySelection.selectedCategories}
							renderTags={(value, props) =>
								value.map((option, index) => (
									<Chip label={option.name} {...props({ index })} />
								))
							}
							renderInput={(params) => <TextField label="Categorías seleccionadas" {...params} />}
						/>
					</Grid>
					<Grid item xs={4} > 
						<TextField 
							fullWidth 
							id="type" 
							disabled
							label="Cantidad minima de preguntas por categoria"
							value={interview.categorySelection.questionsForCategory}
						/>
					</Grid>
			</Grid>	
			<Grid container sx={{ mt: 6 }}>
				<Grid item xs={12}> 
					<QuestionPanel
						isReadOnly
						questions={interview.questions}
						selectedQuestions={interview.questionSelection.selectedQuestions}
					/>
				</Grid>
			</Grid>
		</>
	)
}

export default InterviewConfirmation
