import React, { useState } from 'react';
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Slide from '@mui/material/Slide';
import CircularProgress from '@mui/material/CircularProgress';

const Transition = React.forwardRef(function Transition(props, ref) {
	return <Slide direction="up" ref={ref} {...props} />
})

const interviewCreationView = (props) => { 

	if (props.isLoading) { 
		return (
			<Box sx={{ pt: 8, pb: 6}}>
				<Typography
					variant="h6"
					align="center"
					color="text.primary"
					gutterBottom
				>
					Cargando...
				</Typography>
				<Box sx={{ display: 'flex', justifyContent: 'center', pt: 8 }}>
					<CircularProgress />
				</Box>
			</Box>
		)
	}
	
	return (
		<Container disableGutters component="main" sx={{ pt: 8, pb: 6}}>
			<Typography
				component="h1"
				variant="h2"
				align="center"
				color="text.primary"
				gutterBottom
			>
				Nueva Entrevista
			</Typography>
			<Box sx={{ width: '100%' }}>
				<Stepper activeStep={props.activeStep}>
					{props.steps.map((label, index) => (
						<Step key={index}>
							<StepLabel>{label}</StepLabel>
						</Step>
					))}
				</Stepper>
				<Box sx={{ pt: 8, ml: 8, mr:8 }}>
					<props.CurrentStep {...props} />
				</Box>
				<>
					<Box sx={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', ml: 8, mr:8 }}>
						<Box>
							<Button
								variant="contained"
								onClick={props.onCancel}
								sx={{ mt: 8, mb: 6, mr: 2, background: 'red' }}
							>
								Cancelar
							</Button>
							<Button
								variant="contained"
								onClick={props.onBackStep}
								sx={{ mt: 8, mb: 6 }}
								disabled={props.isFirstStep}
							>
								Volver
							</Button>
						</Box>
						{
							props.isLastStep ?
								<Button
									variant="contained"
									onClick={props.onConfirmInterview}
									sx={{ mt: 8, mb: 6 }}
								>
									Confirmar
								</Button>
								:
								<Button
									variant="contained"
									onClick={props.onNextStep}
									sx={{ mt: 8, mb: 6 }}
								>
									Siguiente paso
								</Button>
						}

					</Box>
				</>
			</Box>
			<Dialog
				open={props.isDialogOpen}
				TransitionComponent={Transition}
				keepMounted
				onClose={props.onDialogClose}
				aria-describedby="alert-dialog-slide-description"
			>
				<DialogTitle>{"¿Desea volver al paso anterior?"}</DialogTitle>
				<DialogContent>
				<DialogContentText id="alert-dialog-slide-description">
					Al volver al paso anterior se borrara toda la información ingresada en el paso actual
				</DialogContentText>
				</DialogContent>
				<DialogActions>
					<Button onClick={props.onDialogClose}>Cancelar</Button>
					<Button onClick={props.onDialogConfirm}>Confirmar</Button>
				</DialogActions>
			</Dialog>
		</Container>
	)
}

export default interviewCreationView
