import React from 'react'
import Box from '@mui/material/Box'
import Paper from '@mui/material/Paper'
import Stack from '@mui/material/Stack'
import { styled } from '@mui/material/styles'
import Typography from '@mui/material/Typography'
import DeleteForeverIcon from '@mui/icons-material/DeleteForever'
import IconButton from '@mui/material/IconButton';

const Item = styled(Paper)(({ theme }) => ({
	backgroundColor: '#3c3737',
	...theme.typography.body2,
	padding: theme.spacing(1),
	textAlign: 'center',
	color: 'white',
	maxWidth: 400,
}));

const Question = props => {
	return (
		<Item
			sx={{
				my: 1,
				p: 2,
				maxWidth: '100%',
			}}
		>
			<Stack spacing={2} direction="row" alignItems="center">
				<Box sx={{ width: '95%', textAlign: 'left', display: 'flex', flexWrap: 'wrap', flexDirection: 'column' }}>
					<Typography sx={{ fontWeight: 'bold' }}>{props.question.title}</Typography>
					<Typography variant="caption" sx={{ fontStyle: 'italic' }}>{props.question.description}</Typography>
				</Box>
				{ (!props.isReadOnly && !props.isLastQuestion) &&
					<Box sx={{ width: '5%', textAlign: 'rigth' }}>
						<IconButton onClick={() => props.onRemoveQuestion(props.question)} >
							<DeleteForeverIcon />
						</IconButton>
					</Box>
				}
			</Stack>
		</Item>
	)
}

Question.defaultProps = {
	onRemoveQuestion: () => {}
}

export default Question
