import * as React from 'react'
import { useTheme } from '@mui/material/styles'
import Box from '@mui/material/Box'
import OutlinedInput from '@mui/material/OutlinedInput'
import InputLabel from '@mui/material/InputLabel'
import MenuItem from '@mui/material/MenuItem'
import FormControl from '@mui/material/FormControl'
import Select from '@mui/material/Select'
import Chip from '@mui/material/Chip'
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import FormHelperText from '@mui/material/FormHelperText';
import { useCreateInterview } from 'stores/create_interview_store'

const ITEM_HEIGHT = 48
const ITEM_PADDING_TOP = 8

const MenuProps = {
	PaperProps: {
		style: {
			maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
			width: 250,
		},
	},
}

function getStyles(name, item, theme) {
	return {
		fontWeight:
			item.indexOf(name) === -1
				? theme.typography.fontWeightRegular
				: theme.typography.fontWeightMedium,
	}
}

const CategoriesSelection = props => {
	const interview = useCreateInterview(state => state)
	const theme = useTheme()

	const handleChange = (event) => {
		const {
			target: { value },
		} = event

		interview.updateCategorySelection({
			...interview.categorySelection,
			selectedCategories: typeof value === 'string' ? value.split(',') : value,
		})
	}

	const handleQuestionsForCategoryChange = e => {
		const target = e.target
		const newValue = e.target.value.replace(/[^0-9]/g, "")
		target.value = newValue
		interview.updateCategorySelection({
			...interview.categorySelection,
			questionsForCategory: Number(newValue),
		})
	}

	const hasQuestionsForCategoryError = props.validations?.questionsForCategory?.hasError
	const hasSelectedCategoriesError = props.validations?.selectedCategories?.hasError

	return (
		<>
			<Typography variant="h5" sx={{ mb: 4}} gutterBottom>
				Seleccione las Categorías
			</Typography>
			<FormControl sx={{ width: '100%' }}>
				<InputLabel id="lbl-categorias">Categorías</InputLabel>
				<Select
					labelId="lbl-categorias"
					id="categorias"
					multiple
					value={interview.categorySelection.selectedCategories}
					onChange={handleChange}
					input={<OutlinedInput id="inp-categorias" label="Categorías" />}
					renderValue={(selected) => (
						<Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
							{selected.map((value) => (
								<Chip key={value.id} label={value.name} />
							))}
						</Box>
					)}
					MenuProps={MenuProps}
					error={hasSelectedCategoriesError}
				>
					{interview.categories.map((category) => (
						<MenuItem
							key={category.id}
							value={category}
							style={getStyles(category.name, interview.categorySelection.selectedCategories, theme)}
						>
							{category.name}
						</MenuItem>
					))}
				</Select>
				<FormHelperText sx={{ color: 'red', }}>
					{hasSelectedCategoriesError ? props.validations.selectedCategories.errorMessage : ''}
				</FormHelperText>
				<TextField 
						sx={{ width: '30%', mt: 4 }}
						id="cantPreguntas" 
						label="Cantidad de Preguntas por Categoría" 
						variant="outlined" 
						fullWidth
						type="number"
						defaultValue={3}
						inputProps={{ max: 11, min: 3 }}
						value={interview.categorySelection.questionsForCategory}
						onInput={handleQuestionsForCategoryChange}
						error={hasQuestionsForCategoryError}
						helperText={hasQuestionsForCategoryError ? props.validations.questionsForCategory.errorMessage : ''}
				/>
			</FormControl>
		</>
	)
}

CategoriesSelection.propTypes = {}

export default CategoriesSelection
