import React, { useEffect } from 'react'
import Typography from '@mui/material/Typography'

import { useCreateInterview } from 'stores/create_interview_store'

import QuestionPanel from 'components/questions/questionsPanel';

const QuestionsSelection = props => {
	const interview = useCreateInterview(state => state)

	const handleRemoveQuestion = deletedQuestion => {
		const selectedQuestions = interview.questionSelection.selectedQuestions
		const newSelectedQuestions = selectedQuestions.map(question => {
			if(question.category.id === deletedQuestion.idCategory) {
				const updatedQuestions = question.selectedQuestions.filter(sQuestion => sQuestion.id != deletedQuestion.id)
				return {
					category: question.category,
					selectedQuestions: updatedQuestions,
				}
			} else {
				return question
			}
		})

		interview.updateQuestionSelection({
			...interview.questionSelection,
			selectedQuestions: newSelectedQuestions,
		})
	}

	// force scroll to top of screen
	useEffect(() => {
		window.scrollTo(0, 0)
	}, [])
	
	return (
		<>
			<Typography variant="h5" sx={{ mb: 4}} gutterBottom autoFocus>
				Selecciones las preguntas por categoría
			</Typography>
			<QuestionPanel
				questions={interview.questions}
				selectedQuestions={interview.questionSelection.selectedQuestions}
				onRemoveQuestion={handleRemoveQuestion}
			/>
		</>
	)
}

QuestionsSelection.propTypes = {}

export default QuestionsSelection
