import * as React from 'react';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LogoPng from 'components/logo/logo';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Copyright from 'components/copyright/copyright';
import { PrimaryButtonLargeSubmit } from 'components/buttons/primaryButton';

const themeOptions = {
	palette: {
		mode: 'light',
		primary: {
			main: '#6F73D2',
			nav: '#6F73D2',
			primaryColor: '#6F73D2',
			secondaryColor: '#00C2CB',
			blackColor: '#031926',
			whiteColor: '#FBFBFF',
			lightPinkColor: '#EEE5E9'
		},
		secondary: {
			main: '#f50057',
		},
	},

}

const defaultTheme = createTheme(themeOptions)

const SignUpView = (props) => {

	const handleSubmit = (event) => {
		event.preventDefault();
		props.handleRegisterUser()
	};

	return (
		<ThemeProvider theme={defaultTheme}>
			<Container component="main" maxWidth="xs">
				<CssBaseline />
				<Box
					sx={{
						marginTop: 8,
						display: 'flex',
						flexDirection: 'column',
						alignItems: 'center',
					}}
				>
					<div>
						<LogoPng />
					</div>
					<Typography component="h1" variant="h5">
						Registrarse
					</Typography>
					<Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
						<Grid container spacing={2}>
							<Grid item xs={12} sm={6}>
								<TextField
									autoComplete="given-name"
									name="firstName"
									required
									fullWidth
									id="firstName"
									label="Nombre"
									autoFocus
									value={props.name}
									onChange={props.handleNameChange}
								/>
							</Grid>
							<Grid item xs={12} sm={6}>
								<TextField
									required
									fullWidth
									id="lastName"
									label="Apellido"
									name="lastName"
									autoComplete="family-name"
									value={props.lastname}
									onChange={props.handleLastnameChange}
								/>
							</Grid>
							<Grid item xs={12}>
								<TextField
									required
									fullWidth
									id="email"
									label="Email"
									name="email"
									autoComplete="email"
									value={props.email}
									onChange={props.handleEmailChange}
								/>
							</Grid>
							<Grid item xs={12}>
								<TextField
									required
									fullWidth
									name="password"
									label="Contraseña"
									type="password"
									id="password"
									value={props.password}
									onChange={props.handlePasswordChange}
								/>
							</Grid>
							<Grid item xs={12}>
								<TextField
									required
									fullWidth
									name="repeatPassword"
									label="Repetir Contraseña"
									type="password"
									id="repeatPassword"
									value={props.repeatPassword}
									onChange={props.handleRepeatPasswordChange}
									error={props.hasRepeatPasswordError}
									helperText={props.hasRepeatPasswordError ? "Las contraseñas no coinciden" : ""}
								/>
							</Grid>
						</Grid>
						<PrimaryButtonLargeSubmit>
							Registrarse
						</PrimaryButtonLargeSubmit>
						<Grid container justifyContent="flex-end">
							<Grid item>
								<Link href="/login" variant="body2">
									¿Ya tienes una cuenta? Ingresá
								</Link>
							</Grid>
						</Grid>
					</Box>
				</Box>
				<Copyright sx={{ mt: 5 }} />
				{/*<Alert variant="filled" severity="success">
					This is a success alert — check it out!
				</Alert>*/}
			</Container>
		</ThemeProvider>
	);
}

export default SignUpView
