import React, { useState } from 'react';
import Box from '@mui/material/Box';
import Select from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography'
import FormHelperText from '@mui/material/FormHelperText';

import { useCreateInterview } from 'stores/create_interview_store'

const InitialSettings = props => {
	const interview = useCreateInterview(state => state)
	
	const handleInterviewTypeChange = (event) => {
		interview.updateInitialSettings({
			...interview.initialSettings,
			interviewTypeId: event.target.value,
		})
	}

	const handleSeniorityLevelChange = (event) => {
		interview.updateInitialSettings({
			...interview.initialSettings,
			seniorityId: event.target.value,
		})
	}

	const handleTitleChange = (event) => {
		interview.updateInitialSettings({
			...interview.initialSettings,
			title: event.target.value,
		})
	}

	const handleAreaChange = (event) => {
		interview.updateInitialSettings({
			...interview.initialSettings,
			area: event.target.value,
		})
	}

	const hasTitleError = props.validations?.title?.hasError
	const hasAreaError = props.validations?.area?.hasError
	const hasInterviewTypeIdError = props.validations?.interviewTypeId?.hasError
	const hasSeniorityIdError = props.validations?.seniorityId?.hasError

	return (
		<>
			<Typography variant="h5" sx={{ mb: 4}} gutterBottom>
				Establezca la Configuración Inicial
			</Typography>
			<Box
				component="form"
				autoComplete="off"
				noValidate
			>
				<Grid container>
					<Grid item xs={8}> 
						<TextField 
							sx={{ pr: 6 }}
							fullWidth 
							id="title" 
							label="Título de la entrevista" 
							variant="outlined" 
							inputProps={{ maxLength: 120 }}
							value={interview.initialSettings.title}
							onChange={handleTitleChange}
							required
							error={hasTitleError}
							helperText={hasTitleError ? props.validations.title.errorMessage : ''}
						/>
					</Grid>
					<Grid item xs={4}>
						<FormControl fullWidth>
							<InputLabel id="interviewType-label" >Tipo de entrevista *</InputLabel>
							<Select
								labelId="interviewType-label"
								id="interviewType"
								value={interview.initialSettings.interviewTypeId}
								onChange={handleInterviewTypeChange}
								fullWidth
								label="Tipo de entrevista"
								required
								error={hasInterviewTypeIdError}
							>
								{
									interview.interviewsTypes.map( (type, index) =>
										<MenuItem value={type.id} key={index}>{type.description}</MenuItem>
									)
								}
							</Select>
							<FormHelperText sx={{ color: 'red', }}>
								{hasInterviewTypeIdError ? props.validations.interviewTypeId.errorMessage : ''}
							</FormHelperText>
						</FormControl>
					</Grid>
					<Grid item xs={8} sx={{mt: 4}}>
						<TextField 
							sx={{ pr: 6 }}
							id="workArea" 
							label="Área de trabajo" 
							variant="outlined" 
							fullWidth
							inputProps={{ maxLength: 120 }}
							value={interview.initialSettings.area}
							onChange={handleAreaChange}
							required
							error={hasAreaError}
							helperText={hasAreaError ? props.validations.area.errorMessage : ''}
						/>
					</Grid>
					<Grid item xs={4} sx={{mt: 4}}>
						<FormControl fullWidth>
							<InputLabel id="seniorityLevel-label" >Nivel de seniority *</InputLabel>
							<Select
								labelId="seniorityLevel-label"
								id="seniorityLevel"
								value={interview.initialSettings.seniorityId}
								label="Nivel de seniority"
								onChange={handleSeniorityLevelChange}
								required
								error={hasSeniorityIdError}
							>
								{
									interview.seniorities.map( (seniority, index) =>
										<MenuItem value={seniority.id} key={index}>{seniority.description}</MenuItem>
									)
								}
							</Select>
							<FormHelperText sx={{ color: 'red', }}>
								{hasSeniorityIdError ? props.validations.seniorityId.errorMessage : ''}
							</FormHelperText>
						</FormControl>
					</Grid>
				</Grid>
			</Box>
		</>
	)
}

InitialSettings.propTypes = {}

export default InitialSettings
