import React from 'react'
import PropTypes from 'prop-types'
import { Outlet } from 'react-router-dom'

import Header from 'components/header/header_component'
import Footer from 'components/footer/footer_component'
import ProtectedRoute from 'routes/guard-route/protected_route'

import CssBaseline from '@mui/material/CssBaseline';
import GlobalStyles from '@mui/material/GlobalStyles';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Container from '@mui/material/Container';

// crear tema: https://zenoo.github.io/mui-theme-creator/
// crear paleta: https://m2.material.io/inline-tools/color/
const themeOptions = {
	palette: {
		mode: 'light',
		primary: {
			main: '#6F73D2',
			nav: '#6F73D2',
			primaryColor: '#6F73D2',
			secondaryColor: '#00C2CB',
			blackColor: '#031926',
			whiteColor: '#FBFBFF',
			redColor: '#DC3545' ,
			lightPinkColor: '#EEE5E9',
			greyColor: '#EEEEEE'
		},
		secondary: {
			main: '#f50057',
		},
	},

}

const defaultTheme = createTheme(themeOptions)

const MasterLayout = () => (
	<ThemeProvider theme={defaultTheme}>
		 <GlobalStyles styles={{ typography: { fontFamily: 'Roboto, Raleway, sans-serif' } }} />
		<CssBaseline />	
		<Container component="header" disableGutters maxWidth={false}>
			<Header/>
		</Container>
		<Container component="main" disableGutters>
			<Outlet/>
		</Container>
		<Container component="footer" maxWidth={false}>
			<Footer/>
		</Container>
	</ThemeProvider>
)

MasterLayout.propTypes = {
	children: PropTypes.any,
}

export default  ProtectedRoute(MasterLayout, 'MasterLayout', [])
