import React from 'react';
import logoImage from 'images/Entrevistarte-isologo.png';
import Link from '@mui/material/Link';

const LogoPng = () => {
  return (
    <div>
      <Link href="/interviews" underline="none" sx={{ mx: 1 }}>
      <img src={logoImage} alt="Logo" style={{ width: '50px', height: '50px', marginRight: '10px'}}/>
      </Link>
    </div>
  );
};
  
export default LogoPng;
