import React from 'react';
import Button from '@mui/material/Button';
import { useTheme } from '@mui/material/styles';

function PrimaryButtonSmall({ children, onClick }) {
    const theme = useTheme();

    return (
        <Button
            variant="contained"
            style={{
                backgroundColor: theme.palette.primary.primaryColor,
                color: theme.palette.primary.whiteColor,
            }}
            onClick={onClick}
            sx={{ mt: 4, mb: 6 }}
        >
            {children}
        </Button>
    );
}

function PrimaryButtonSmallSubmit({ children , onSubmit }) {
    const theme = useTheme();

    return (
        <Button
            variant="contained"
            type="submit"
            onSubmit={onSubmit} 
            style={{
                backgroundColor: theme.palette.primary.primaryColor,
                color: theme.palette.primary.whiteColor,
            }}
            sx={{ mt: 4, mb: 6 }}
        >
            {children}
        </Button>
    );
}

