import React from 'react'

import SignUpView from 'views/sign-up/sign_up_view'
import { useCreateUser, defaultState } from 'stores/create_user'
import { createUser } from 'services/firebase'

const SignUpContainer = () => {
	const user = useCreateUser(state => state)

	const onNameChange = (event) => {
		user.setName(event.target.value)
	}

	const onLastnameChange = (event) => {
		user.setLastname(event.target.value)
	}

	const onEmailChange = (event) => {
		user.setEmail(event.target.value)
	}

	const onPasswordChange = (event) => {
		user.setPassword(event.target.value)
	}

	const onRepeatPasswordChange = (event) => {
		user.setRepeatPassword(event.target.value)
	}

	const hasRepeatPasswordError = user.password !== user.repeatPassword

	const onRegisterUser = (event) => {
		if (!hasRepeatPasswordError) {
			createUser(user.email, user.password)
		}
	}

	return (
		<SignUpView
			name={user.name}
			lastname={user.lastname}
			email={user.email}
			password={user.password}
			repeatPassword={user.repeatPassword}
			handleNameChange={onNameChange}
			handleLastnameChange={onLastnameChange}
			handleEmailChange={onEmailChange}
			handlePasswordChange={onPasswordChange}
			handleRepeatPasswordChange={onRepeatPasswordChange}
			hasRepeatPasswordError={hasRepeatPasswordError}
			handleRegisterUser={onRegisterUser}
		/>
	)
}

SignUpContainer.propTypes = {}

export default SignUpContainer
