import { interviewCreationSteps } from "enums/interviewCreationSteps"

const validateInitialSettings = interview => {
	let validationObject = {
		title: {
			hasError: false,
			errorMessage: 'Ingrese un título',
		},
		area: {
			hasError: false,
			errorMessage: 'Ingrese un área de trabajo',
		},
		interviewTypeId: {
			hasError: false,
			errorMessage: 'Seleccione un tipo de entrevista',
		},
		seniorityId: {
			hasError: false,
			errorMessage: 'Seleccione el nivel de seniority',
		},
		hasError: false
	}

	if(!interview.initialSettings.title || interview.initialSettings.title == '') {
		validationObject.title.hasError = true
		validationObject.hasError = true
	}
	if(!interview.initialSettings.area || interview.initialSettings.area == '') {
		validationObject.area.hasError = true
		validationObject.hasError = true
	}
	if(!interview.initialSettings.interviewTypeId || interview.initialSettings.interviewTypeId <= 0) {
		validationObject.interviewTypeId.hasError = true
		validationObject.hasError = true
	}
	if(!interview.initialSettings || interview.initialSettings.seniorityId <= 0) {
		validationObject.seniorityId.hasError = true
		validationObject.hasError = true
	}
	return validationObject
}

const validateCategorySelection = interview => {
	const validationObject = {
		selectedCategories: {
			hasError: false,
			errorMessage: 'Seleccione una categoría',
		},
		questionsForCategory: {
			hasError: false,
			errorMessage: 'Ingrese un valor entre 3 y 9',
		},
		hasError: false
	}
	
	if(!interview.categorySelection.questionsForCategory 
		|| Number(interview.categorySelection.questionsForCategory) < 3
		|| Number(interview.categorySelection.questionsForCategory > 9) ) {
		validationObject.questionsForCategory.hasError = true
		validationObject.hasError = true
	}
	if(!interview.categorySelection.selectedCategories || interview.categorySelection.selectedCategories <= 0) {
		validationObject.selectedCategories.hasError = true
		validationObject.hasError = true
	}
	return validationObject
}

const validateQuestionSelection = interview => {
	// no validations for this step
}

const validationsForStep = {
	[interviewCreationSteps.INITIAL_CONFIG]: validateInitialSettings,
	[interviewCreationSteps.CATEGORY_SELECTION]: validateCategorySelection,
	[interviewCreationSteps.QUESTIONS_SELECTION]: validateQuestionSelection,
}

const validateStep = interview => {
	return validationsForStep[interview.activeStep](interview)
}

export default validateStep
