import { create } from 'zustand'
import { getDate } from 'helpers/date'

const defaultState = {
	// steps
	initialSettings: {
		title: '',
		area: '',
		interviewTypeId: 0,
		seniorityId: 0,
		userId: 0,
		date: getDate(),
	},
	categorySelection: {
		selectedCategories: [],
		questionsForCategory: 3,
	},
	questionSelection: {
		selectedQuestions: [],
	},

	// fetched data
	interviewsTypes: [],
	seniorities: [],
	categories: [],
	questions: [],

	// general data
	activeStep: 0,
	isLoading: true,
	isDialogOpen: false,
}

const useCreateInterview = create( (set, get) => ({
	...defaultState,

	// setters
	setInterviewsTypes: interviews => {
		set({ interviewsTypes: interviews })
	},
	setSeniorities: seniorities => {
		set({ seniorities: seniorities })
	},
	setCategories: categories => {
		set({ categories: categories })
	},
	setQuestions: questions => {
		set({ questions: questions })
	},
	setActiveStep: step => {
		set({ activeStep: step })
	},
	setDialog: isOpen => {
		set({ isDialogOpen: isOpen })
	},
	setIsLoading: isLoading => {
		set({ isLoading })
	},

	// update functions
	updateInitialSettings: data => {
		set({ initialSettings: {...data} })
	},
	updateCategorySelection: data => {
		set({ categorySelection: {...data} })
	},
	updateQuestionSelection: data => {
		set({ questionSelection: {...data} })
	},
}))

export {
	useCreateInterview,
	defaultState,
}