import React from 'react'
import PropTypes from 'prop-types'
import axios from "axios"
import { useNavigate } from 'react-router-dom'

import InterviewsView from 'views/interviews/interviews_view'

const interviews = [
	{
		id: 0,
		title: 'Técnica Desarrollo Pasantes',
		area: 'Desarrollo',
		InterviewType: 'Técnica',
		Seniority: 'Junior',
		date: '10/01/2022',
		categories: ['Programacion', 'Base de datos'],
	},
	{
		id: 1,
		title: 'Puesto de Gerencia',
		area: 'Gerente Marketing',
		InterviewType: 'Personal',
		Seniority: 'Senior',
		date: '15/05/2021',
		categories: ['Trabajo en equipo', 'Liderazgo', 'Gestión'],
	},
]

const API = axios.create({
	baseURL: process.env.REACT_APP_BACKEND_API
})

const InterviewsContainer = props => {
	const navigate = useNavigate()

	const onCreate = () => {
		navigate('/interviews/create')
	}

	const handleViewInterview = id => {
		navigate(`/interviews/${id}`)
	}

	const handleEditInterview = id => {
		navigate(`/interviews/edit/${id}`)
	}

	const handleTakeInterview = id => {
		navigate(`/interviews/take/${id}`)
	}

	return (
		<InterviewsView
			interviews={interviews}
			onHandleCreate={onCreate}
			onViewInterview={handleViewInterview}
			onEditInterview={handleEditInterview}
			onTakeInterview={handleTakeInterview}
		/>
	)
}

InterviewsContainer.propTypes = {}

export default InterviewsContainer
