import * as React from 'react';

import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';

import IconButton from '@mui/material/IconButton';
import AddIcon from '@mui/icons-material/Add';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import PersonSearchIcon from '@mui/icons-material/PersonSearch';

import { PrimaryButtonLarge } from 'components/buttons/primaryButton';

const InterviewsView = props => {
	return (
		<>
			<Container disableGutters maxWidth="sm" component="main" sx={{ pt: 8 }}>
				<Typography
					component="h1"
					variant="h2"
					align="center"
					color="text.primary"
					gutterBottom
				>
					Mis Entrevistas
				</Typography>
				<Typography variant="h5" align="center" color="text.secondary" component="p">
					Visualizá todas tus entrevistas en un solo lugar, de manera rápida y sencilla
				</Typography>
				<PrimaryButtonLarge onClick={props.onHandleCreate}>Nueva Entrevista</PrimaryButtonLarge>
			</Container>
			<Container sx={{ py: 8 }}>
				<TableContainer component={Paper}>
					<Table sx={{ minWidth: 650 }} aria-label="simple table">
						<TableHead>
							<TableRow>
								<TableCell>Título</TableCell>
								<TableCell>Área de trabajo</TableCell>
								<TableCell>Tipo de entrevista</TableCell>
								<TableCell>Área de trabajo</TableCell>
								<TableCell>Nivel de seniority</TableCell>
								<TableCell>Categorias</TableCell>
								<TableCell align="right">Acciones</TableCell>
							</TableRow>
						</TableHead>
						<TableBody>
							{props.interviews.map((row, index) => (
								<TableRow
									key={index}
									sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
								>
									<TableCell component="th" scope="row">
										{row.name}
									</TableCell>
									<TableCell component="th" scope="row">
										{row.name}
									</TableCell>
									<TableCell component="th" scope="row">
										{row.name}
									</TableCell>
									<TableCell component="th" scope="row">
										{row.name}
									</TableCell>
									<TableCell component="th" scope="row">
										{row.name}
									</TableCell>
									<TableCell component="th" scope="row">
										{row.name}
									</TableCell>
									<TableCell align="right">
										<IconButton onClick={() => { props.onViewInterview(row.id)}} >
											<AddIcon />
										</IconButton>
										<IconButton onClick={() => { props.onEditInterview(row.id)}} >
											<RemoveRedEyeIcon />
										</IconButton>
										<IconButton onClick={() => { props.onTakeInterview(row.id)}} >
											<PersonSearchIcon />
										</IconButton>
									</TableCell>
								</TableRow>
							))}
						</TableBody>
					</Table>
				</TableContainer>
			</Container>
		</>
	)
}

InterviewsView.propTypes = {}

export default InterviewsView
