import React from 'react'
import PropTypes from 'prop-types'

import InterviewTakenView from 'views/interviews/interview_taken_view'

const interview = {
	initialSettings: {
		title: '',
		date: '',
		descriptions: '',
		
	}
}

const InterviewTakenContainer = props => {
	return (
		<InterviewTakenView interview={interview} />
	)
}

InterviewTakenContainer.propTypes = {}

export default InterviewTakenContainer