import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardHeader from '@mui/material/CardHeader';
import Grid from '@mui/material/Grid';
import StarIcon from '@mui/icons-material/StarBorder';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';

const tiers = [
  {
    title: 'Free',
    price: '0',
    description: [
      'Creación ilimitada de entrevistas básicas.',
      'Acceso a preguntas prediseñadas por categoría.',
      'Registro y almacenamiento de entrevistas pasadas (límitado).',
      'Acceso a métricas básicas de entrevistas realizadas.',
    ],
    buttonText: 'Suscribirse',
    buttonVariant: 'outlined',
  },
  {
    title: 'Pro',
    subheader: 'Mas popular',
    price: '15',
    description: [
		'Creación de entrevistas avanzadas.',
		'Asociación de entrevistas con datos específicos del entrevistado.',
		'Acceso a métricas.',
		'Soporte prioritario por correo electrónico.',
    ],
    buttonText: 'Suscribirse',
    buttonVariant: 'contained',
  },
  {
    title: 'Enterprise',
    price: '30',
    description: [
		'Creación ilimitada de entrevistas avanzadas.',
		'Herramientas avanzadas de análisis de texto.',
		'Acceso a métricas.',
		'Soporte premium 24/7 por correo electrónico y chat en vivo.',
    ],
    buttonText: 'Suscribirse',
    buttonVariant: 'outlined',
  },
];


export default function Pricing() {
  return (
	<>
		<Container disableGutters maxWidth="sm" component="main" sx={{ pt: 8, pb: 6 }}>
			<Typography
			component="h1"
			variant="h2"
			align="center"
			color="text.primary"
			gutterBottom
			>
			Precios
			</Typography>
			<Typography variant="h5" align="center" color="text.secondary" component="p">
			Explora Nuestros Packs
			</Typography>
		</Container>
		<Container maxWidth="md" component="main">
			<Grid container spacing={5} alignItems="flex-end">
			{tiers.map((tier) => (
				// Enterprise card is full width at sm breakpoint
				<Grid
				item
				key={tier.title}
				xs={12}
				sm={tier.title === 'Enterprise' ? 12 : 6}
				md={4}
				>
				<Card>
					<CardHeader
					title={tier.title}
					subheader={tier.subheader}
					titleTypographyProps={{ align: 'center' }}
					action={tier.title === 'Pro' ? <StarIcon /> : null}
					subheaderTypographyProps={{
						align: 'center',
					}}
					sx={{
						backgroundColor: (theme) =>
						theme.palette.mode === 'light'
							? theme.palette.grey[200]
							: theme.palette.grey[700],
					}}
					/>
					<CardContent>
					<Box
						sx={{
						display: 'flex',
						justifyContent: 'center',
						alignItems: 'baseline',
						mb: 2,
						}}
					>
						<Typography component="h2" variant="h3" color="text.primary">
						${tier.price}
						</Typography>
						<Typography variant="h6" color="text.secondary">
						/mes
						</Typography>
					</Box>
					<ul>
						{tier.description.map((line) => (
						<Typography
							component="li"
							variant="subtitle1"
							align="center"
							key={line}
						>
							{line}
						</Typography>
						))}
					</ul>
					</CardContent>
					<CardActions>
					<Button fullWidth variant={tier.buttonVariant}>
						{tier.buttonText}
					</Button>
					</CardActions>
				</Card>
				</Grid>
			))}
			</Grid>
		</Container>
	</>
  );
}