import React, { useEffect, useState } from 'react'
import axios from "axios"

import InterviewCreationView from 'views/interviews/creation/interview_creation_view'
import InitialSettings from 'views/interviews/creation/initial_settings'
import CategoriesSelection from 'views/interviews/creation/categories_selection'
import QuestionsSelection from 'views/interviews/creation/questions_selection'
import InterviewConfirmation from 'views/interviews/creation/interview_confirmation'
import { useCreateInterview, defaultState } from 'stores/create_interview_store'
import { useNavigate } from 'react-router-dom'
import { interviewCreationSteps, interviewCreationStepTitles } from "enums/interviewCreationSteps"
import validateStep from 'helpers/validations/interview_creation_validation'

const mapComponentByStep = {
	[interviewCreationSteps.INITIAL_CONFIG]: InitialSettings,
	[interviewCreationSteps.CATEGORY_SELECTION]: CategoriesSelection,
	[interviewCreationSteps.QUESTIONS_SELECTION]: QuestionsSelection,
	[interviewCreationSteps.INTERVIEW_CONFIRMATION]: InterviewConfirmation,
}

const API = axios.create({
	baseURL: process.env.REACT_APP_BACKEND_API
})

const InterviewCreationContainer = props => {
	const [validations, setValidations] = useState()
	const interview = useCreateInterview(state => state)
	const navigate = useNavigate()

	// Fetch initial data
	useEffect(() => {
		const getInterviewTypes = async () => {
			const response = await API.get("/api/InterviewType")
			interview.setInterviewsTypes(response.data)
		}

		const getSeniorities = async () => {
			const response = await API.get("/api/Seniority")
			interview.setSeniorities(response.data)
		}
		
		getInterviewTypes()
		getSeniorities()
		interview.setIsLoading(false)
	}, [])

	// fetch categories when interviewTypeId change
	useEffect(() => {
		interview.setIsLoading(true)

		const getCategoriesbyInterviewType = async () => {
			const response = await API.get(`/api/Category?idInterviewType=${interview.initialSettings.interviewTypeId}`)
			interview.setCategories(response.data)
		}
		
		getCategoriesbyInterviewType()
		interview.setIsLoading(false)
	}, [interview.initialSettings.interviewTypeId])

	// fetch questions when categories changes
	useEffect(() => {
		interview.setIsLoading(true)

		const getQuestionsByCategoryAndSeniority = async (categoryId) => {
			const response = await API.get(`/question/${categoryId}/${interview.initialSettings.seniorityId}`)
			return response.data
		}
		
		let questionSelection = [...interview.questionSelection.selectedQuestions]

		// iterate the categories and fetch questions
		interview.categorySelection.selectedCategories.map(async category => {
			// just get unfetched categories
			if(!interview.questions.some(question => question.idCategory === category.id))
			{
				const newQuestions = await getQuestionsByCategoryAndSeniority(category.id)
				const allQuestions = [...interview.questions, ...newQuestions]
				interview.setQuestions(allQuestions)

				// get first X amount of questions
				const newSelectedQuestions = newQuestions.slice(0, interview.categorySelection.questionsForCategory)

				// create a new object with the questions and his category
				const newQuestionSelection = [{
					category: {...category},
					selectedQuestions: newSelectedQuestions,
				}]

				// combine the current questions already stored and the new ones
				questionSelection.push(...newQuestionSelection)
			}
		})

		// update the questions in the store
		interview.updateQuestionSelection({
			...interview.questionSelection,
			selectedQuestions: questionSelection,
		})

		interview.setIsLoading(false)
	}, [interview.categorySelection.selectedCategories])

	useEffect(() => {
		interview.setIsLoading(true)
		
		let questionSelection = [...interview.questionSelection.selectedQuestions]

		// iterate the categories and fetch questions
		interview.categorySelection.selectedCategories.map(category => {
			// get question of the current category
			const questionsByCategory = interview.questions.filter(question => question.idCategory === category.id)

			// get first X amount of questions
			const newSelectedQuestions = questionsByCategory.slice(0, interview.categorySelection.questionsForCategory)

			// create a new object with the questions and his category
			const newQuestionSelection = [{
				category: {...category},
				selectedQuestions: newSelectedQuestions,
			}]

			// combine the current questions already stored and the new ones
			questionSelection = [...questionSelection, ...newQuestionSelection]
		})

		// update the questions in the store
		interview.updateQuestionSelection({
			...interview.questionSelection,
			selectedQuestions: questionSelection,
		})

		interview.setIsLoading(false)
	}, [interview.categorySelection.questionsForCategory])

	// Handle dialog and steps movement
	const handleOpenDialog = () => interview.setDialog(true)

	const handleDialogConfirm = () => {
		let step = interview.activeStep - 1
		if (step < 0) { 
			step = 0
		}
		interview.setActiveStep(step)
		interview.setDialog(false)

		// clear steps
		if (step === 0) {
			interview.updateCategorySelection(defaultState.categorySelection)
		} else if (step === 1) {
			interview.updateQuestionSelection(defaultState.questionSelection)
		}
	}

	const handleDialogClose = () => interview.setDialog(false)

	const isLastStep = interview.activeStep === (interviewCreationStepTitles.length - 1)
	const isFirstStep = interview.activeStep === 0

	const handleNextStep = () => {
		// validations of the current step
		const stepValidation = validateStep(interview)
		setValidations(stepValidation)

		// logic to move to next step
		if (!stepValidation?.hasError) {
			let step = interview.activeStep + 1
			if (isLastStep || step > interviewCreationStepTitles.length -1) { 
				step = interviewCreationStepTitles.length - 1
			}
			interview.setActiveStep(step)
		}
	}

	const postInterview = async () => {
		const currentUser = localStorage.getItem('currentUser')
		const userData = currentUser ? JSON.parse(currentUser) : false

		if(!userData) {
			// mostrar error
			navigate('/')
		}

		const questionsIds = []
		interview.questionSelection.selectedQuestions.forEach(item => {
			item.selectedQuestions.forEach(question => {
				if (!questionsIds.some(qid => qid === question.id)) {
					questionsIds.push(question.id)
				}
			})
		})

		const createObject = {
			title: interview.initialSettings.title,
			area: interview.initialSettings.area,
			idInterviewType: interview.initialSettings.interviewTypeId,
			idSeniority: interview.initialSettings.seniorityId,
			idUser: userData.uid,
			date: interview.initialSettings.date,
			questionsIds,
		}

		console.log("createObject", createObject)

		const response = await API.post("/api/Interview", createObject, {
			headers: {
				"Content-Type": "application/json",
			}
		})
		console.log("response", response)
		if(response.error) {
			// mostrar error
		}
	}

	const handleConfirmInterview = () => {
		postInterview()
		//navigate('/')
	}

	const handleCancel = () => {
		interview.setActiveStep(0)
		interview.updateInitialSettings(defaultState.initialSettings)
		interview.updateCategorySelection(defaultState.categorySelection)
		interview.updateQuestionSelection(defaultState.questionSelection)
		navigate('/')
	}

	return (
		<InterviewCreationView 
			steps={interviewCreationStepTitles}
			activeStep={interview.activeStep}
			onNextStep={handleNextStep}
			onBackStep={handleOpenDialog}
			isLastStep={isLastStep}
			isFirstStep={isFirstStep}
			CurrentStep={mapComponentByStep[interview.activeStep]}
			onDialogConfirm={handleDialogConfirm}
			onDialogClose={handleDialogClose}
			isDialogOpen={interview.isDialogOpen}
			isLoading={interview.isLoading}
			onConfirmInterview={handleConfirmInterview}
			validations={validations}
			onCancel={handleCancel}
		/>
	)
}

InterviewCreationContainer.propTypes = {}

export default InterviewCreationContainer