import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import LogoPng from 'components/logo/logo';
import Typography from '@mui/material/Typography';
import Copyright from 'components/copyright/copyright';
import GlobalStyles from '@mui/material/GlobalStyles';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { PrimaryButtonLargeSubmit } from 'components/buttons/primaryButton';

const themeOptions = {
	palette: {
		mode: 'light',
		primary: {
			main: '#6F73D2',
			nav: '#6F73D2',
			primaryColor: '#6F73D2',
			secondaryColor: '#00C2CB',
			blackColor: '#031926',
			whiteColor: '#FBFBFF',
			lightPinkColor: '#EEE5E9'
		},
		secondary: {
			main: '#f50057',
		},
	},

}

const defaultTheme = createTheme(themeOptions)

const LoginView = props => {

	const handleSubmit = e => {
		e.preventDefault()
		const data = new FormData(e.currentTarget)
		props.onLogIn(
			data.get('email'),
			data.get('password')

		)
	}

	return (
		<ThemeProvider theme={defaultTheme}>
			<GlobalStyles styles={{ typography: { fontFamily: 'Roboto, Raleway, sans-serif' } }} />
			<CssBaseline />
			<Grid container component="main" sx={{ height: '100vh' }}>
				<Grid
					item
					xs={false}
					sm={4}
					md={7}
					sx={{
						backgroundImage: 'url(https://www.bizneo.com/blog/wp-content/uploads/2023/02/entrevista-de-seleccion-1.jpg)',
						backgroundRepeat: 'no-repeat',
						backgroundSize: 'cover',
						backgroundPosition: 'center',
					}}
				/>
				<Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
					<Box
						sx={{
							my: 8,
							mx: 4,
							display: 'flex',
							flexDirection: 'column',
							alignItems: 'center',
						}}
					>
						<div>
							<LogoPng />
						</div>
						<Typography component="h1" variant="h5">
							Entrevistarte
						</Typography>
						<Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 1 }}>
							<TextField
								margin="normal"
								required
								fullWidth
								id="email"
								label="Email"
								name="email"
								autoComplete="email"
								autoFocus
								defaultValue={'test@entrevistarte.com'}
							/>
							<TextField
								margin="normal"
								required
								fullWidth
								name="password"
								label="Contraseña"
								type="password"
								id="password"
								autoComplete="current-password"
								defaultValue={'Unlam2023'}
							/>
							<FormControlLabel
								control={<Checkbox value="remember" color="primary" />}
								label="Recordarme"
							/>
							<PrimaryButtonLargeSubmit>
								Ingresar
							</PrimaryButtonLargeSubmit>
							<Grid container>
								<Grid item xs>
									<Link href="#" variant="body2">
										¿Olvidaste tu contraseña?
									</Link>
								</Grid>
								<Grid item>
									<Link onClick={props.onSignUp} variant="body2">
										{"¿No tienes una cuenta? Registrate"}
									</Link>
								</Grid>
							</Grid>
							<Copyright sx={{ mt: 5 }} />
						</Box>
					</Box>
				</Grid>
			</Grid>
		</ThemeProvider>
	);
}

LoginView.propTypes = {}

export default LoginView
