import React from 'react'
import PropTypes from 'prop-types'

import InterviewTakeView from 'views/interviews/interview_take_view'

const interview = {
	initialSettings: {
		title: '',
		date: '',
		descriptions: '',
		
	}
}

const InterviewTakeContainer = props => {
	return (
		<InterviewTakeView interview={interview} />
	)
}

InterviewTakeContainer.propTypes = {}

export default InterviewTakeContainer