import React from 'react'
import PropTypes from 'prop-types'

import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

import QuestionToFill from 'components/candidate-interview/question_to_fill'

const boxStyle = {
	border: '1px solid #0000003b',
		borderRadius: '6px',
		boxShadow: '0px 2px 1px -1px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12)',
		textAlign: 'center',
	flexGrow: '1',
	overflow: 'hidden',
	p: 3,
	position: 'relative',
	mb: 4,
	mt: 4,
}


const InterviewTakeView = ({interview}) => {
	return (
		<>
			<Grid container sx={{ mb: 4 , mt: 4 }}>
				<Grid item xs={8} >
					<TextField
						sx={{ pr: 6 }}
						fullWidth
						id="title"
						disabled
						label="Título de la entrevista"
						value={'Título de la entrevista'}
					/>
				</Grid>
				<Grid item xs={4}>
					<TextField
						fullWidth
						id="date"
						disabled
						label="Fecha"
						value={'25/05/2023'}
					/>
				</Grid>
			</Grid>
			<Grid container sx={{ mb: 4 }}>
				<Grid item xs={4}>
					<TextField
						sx={{ pr: 6 }}
						fullWidth
						id="type"
						disabled
						label="Tipo de entrevista"
						value={'Tipo de entrevista'}
					/>
				</Grid>
				<Grid item xs={4}>
					<TextField
						sx={{ pr: 6 }}
						fullWidth
						id="type"
						disabled
						label="Nivel de seniority"
						value={'Nivel de seniority'}
					/>
				</Grid>
				<Grid item xs={4}>
					<TextField
						fullWidth
						id="type"
						disabled
						label="Area de trabajo"
						value={'Area de trabajo'}
					/>
				</Grid>
			</Grid>
			<Grid container sx={{ mt: 4 }}>
				<Grid item xs={12} >
					<TextField
						fullWidth
						id="candidate"
						label="Nombre del candidato"
						value={'Juan Jose de San Martin'}
					/>
				</Grid>
			</Grid>
			<Grid container sx={{ mt: 4 }}>
				<Grid item xs={12} >
					<TextField
						fullWidth
						id="summary"
						label="Resumen del candidato"
						value={'Fue un militar chileno que comandó las fuerzas del Regimiento 4.º de Línea en la Toma del Morro de Arica, durante la Guerra del Pacífico, combate en el cual falleció'}
						multiline
						rows={2}
					/>
				</Grid>
			</Grid>
			<Grid container sx={{ mb: 4 }}>
				<Box sx={boxStyle}>
					<Typography variant="h6" gutterBottom>
						{'Trabajo en equipo'}
					</Typography>
					<QuestionToFill />
					<QuestionToFill />
					<QuestionToFill />
				</Box>
				<Box 
					sx={{ 
						display: 'flex',
						flexDirection: 'row',
						justifyContent: 'space-between',
						ml: 8,
						mr:8, 
						width: '100%',
					}}
				>
						<Button
							variant="contained"
							onClick={() => {}}
							sx={{ mt: 4, mb: 4 }}
						>
							Cancelar
						</Button>
						<Button
							variant="contained"
							size='large'
							onClick={() => {}}
							sx={{ mt: 4, mb: 4, width: '30%' }}
							disabled={true}
						>
							Confirmar
						</Button>
				</Box>
			</Grid>
		</>
	)
}

InterviewTakeView.propTypes = {}

export default InterviewTakeView