import { create } from 'zustand'

const defaultState = {
    name: '',
    lastname: '',
    email: '',
    password: '',
    repeatPassword: '',
}

const useCreateUser = create((set, get) => ({
    ...defaultState,

    setName: newName => {
        set({name: newName})
    },

    setLastname: newLastname => {
        set({lastname: newLastname})
    },

    setEmail: newEmail => {
        set({email: newEmail})
    },

    setPassword: newPassword => {
        set({password: newPassword})
    },

    setRepeatPassword: newRepeatPassword => {
        set({repeatPassword: newRepeatPassword})
    }
})
)

export {
	useCreateUser,
	defaultState,
}