import React from 'react'
import PropTypes from 'prop-types'

import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box'
import Button from '@mui/material/Button';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

function createData(name, calories, fat, carbs, protein) {
	return { name, calories, fat, carbs, protein };
}

const rows = [
	createData('Julian Antonuccio', '25/05/2023'),
	createData('Tomas Garcia', '05/02/2023'),
	createData('Serena Donato', '20/01/2023'),
	createData('Silvio Allegre', '15/04/2023'),
];

const InterviewTakenView = ({ interview }) => {
	return (
		<>
			<Grid container sx={{ mb: 4, mt: 4 }}>
				<Grid item xs={8} >
					<TextField
						sx={{ pr: 6 }}
						fullWidth
						id="title"
						disabled
						label="Título de la entrevista"
						value={'Título de la entrevista'}
					/>
				</Grid>
				<Grid item xs={4}>
					<TextField
						fullWidth
						id="date"
						disabled
						label="Fecha de creación"
						value={'Fecha'}
					/>
				</Grid>
			</Grid>
			<Grid container sx={{ mb: 4 }}>
				<Grid item xs={4}>
					<TextField
						sx={{ pr: 6 }}
						fullWidth
						id="type"
						disabled
						label="Tipo de entrevista"
						value={'Tipo de entrevista'}
					/>
				</Grid>
				<Grid item xs={4}>
					<TextField
						sx={{ pr: 6 }}
						fullWidth
						id="type"
						disabled
						label="Nivel de seniority"
						value={'Nivel de seniority'}
					/>
				</Grid>
				<Grid item xs={4}>
					<TextField
						fullWidth
						id="type"
						disabled
						label="Area de trabajo"
						value={'Area de trabajo'}
					/>
				</Grid>
			</Grid>
			<Grid container sx={{ mb: 4 }}>
			<TableContainer component={Paper}>
					<Table sx={{ minWidth: 650 }} aria-label="simple table">
						<TableHead>
							<TableRow>
								<TableCell>Candidato</TableCell>
								<TableCell align="right">Fecha</TableCell>
								<TableCell align="right">Detalle</TableCell>
							</TableRow>
						</TableHead>
						<TableBody>
							{rows.map((row) => (
								<TableRow
									key={row.name}
									sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
								>
									<TableCell component="th" scope="row">
										{row.name}
									</TableCell>
									<TableCell align="right">{row.calories}</TableCell>
									<TableCell align="right">
									<Button
										variant="contained"
										onClick={() => {}}
										sx={{}}
									>
										ver
									</Button>
									</TableCell>
								</TableRow>
							))}
						</TableBody>
					</Table>
				</TableContainer>
				<Box
					sx={{
						display: 'flex',
						flexDirection: 'row',
						justifyContent: 'center',
						ml: 8,
						mr: 8,
						width: '100%',
					}}
				>
					<Button
						variant="contained"
						onClick={() => { }}
						sx={{ mt: 4, mb: 4 }}
					>
						Volver
					</Button>
				</Box>
			</Grid>
		</>
	)
}

InterviewTakenView.propTypes = {}

export default InterviewTakenView