import React, { useState } from 'react'
import Rating from '@mui/material/Rating';
import Box from '@mui/material/Box';
import StarIcon from '@mui/icons-material/Star';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Divider from '@mui/material/Divider';
import Chip from '@mui/material/Chip';

const labels = {
	0: 'Muy Mal',
	1: 'Mal',
	2: 'Normal',
	3: 'Bien',
	4: 'Muy bien',
	5: 'Excelente',
}

function getLabelText(value) {
	return `${value} Star${value !== 1 ? 's' : ''}, ${labels[value]}`
}

const QuestionToFill = props => {
	const [value, setValue] = useState(2)
	const [hover, setHover] = useState(-1)

	return (
		<Box
			sx={{
				display: 'flex',
				alignItems: 'center',
				flexWrap: 'wrap',
				minHeight: '120px',
				mt: 2,
				mb: 2,
			}}
		>
			<Box
				sx={{
					display: 'flex',
					alignItems: 'left',
					width: '50%',
					flexWrap: 'wrap',
					flexDirection: 'column',
				}}
			>
				<Typography variant="h6" component="h4" gutterBottom sx={{ width: '100%', textAlign: 'left' }}>
					{'¿Cuál es el papel de la comunicación efectiva en el entorno laboral y cómo puede impactar en el éxito de un proyecto?'}
				</Typography>
				<Typography variant="p" component="p" gutterBottom sx={{ width: '100%', textAlign: 'left', fontStyle: 'italic', fontSize: '14px' }}>
					{'Explique la importancia de la comunicación efectiva en el trabajo.'}
				</Typography>
				<Box
					sx={{
						display: 'flex',
						flexWrap: 'nowrap',
						alignItems: 'center'
					}}
				>
					<Rating
						sx={{
						}}
						size="large"
						name="hover-feedback"
						value={value}
						precision={1}
						getLabelText={getLabelText}
						onChange={(event, newValue) => {
							setValue(newValue);
						}}
						onChangeActive={(event, newHover) => {
							setHover(newHover);
						}}
						emptyIcon={<StarIcon style={{ opacity: 0.55 }} fontSize="inherit" />}
					/>
					{value !== null && (
						<Box sx={{ ml: 2 }}>{labels[hover !== -1 ? hover : value]}</Box>
					)}
				</Box>
			</Box>
			<Box
				sx={{
					width: '50%',
					display: 'flex',
					minHeight: '120px',
				}}
			>
				<TextField
					multiline={true}
					minRows={2}
					maxRows={4}
					sx={{
						"& .MuiInputBase-root": {
							minHeight: '100%'
						}
					}}
					fullWidth
					label="Respuesta"
					id="fullWidth"
				/>
			</Box>
			<Divider
				sx={{
					width:'100%',
					mt:2,
				}}
			>
				<Chip label="" />
			</Divider>
		</Box>
	)
}

QuestionToFill.propTypes = {}

export default QuestionToFill
